.modal {
    --transition-time: 0.3s;
    font-family: sans-serif;
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    z-index: 100;
    background-color: rgba(0,0,0, 0.5);
    color: rgb(64, 88, 83);
    cursor: pointer;
    overflow-y: auto;
    overflow-x: hidden;
    text-align: center;
    opacity: 0;
    visibility: hidden;
    transition: opacity var(--transition-time), visibility var(--transition-time);
}

.modal::before {
    content: '';
    display: inline-block;
    vertical-align: middle;
    height: 100%;
}

.modal__container {
    position: relative;
    /* width: 90%; */
    max-width: 1000px;
    margin: 50px auto;
    display: none;
    /* display: inline-block; */
    vertical-align: middle;
    background-color: #fff;
    border-radius: 5px;
    padding: 20px;
    cursor: default;
    text-align: left;
}

@media (max-width: 1024px) {
    .modal__container {
        width: 90%;
    }
}

.modal.is-open {
    opacity: 1;
    visibility: visible;
    transition: opacity var(--transition-time), visibility var(--transition-time);
}

.modal__container.modal-open {
    display: inline-block;
}

.fade {
    opacity: 0;
    transition: opacity var(--transition-time);
}

.fade.animate-open {
    opacity: 1;
    transition: opacity var(--transition-time);
}

.fadeInUp {
    opacity: 0;
    transform: translateY(-100px);
    transition: opacity var(--transition-time), transform var(--transition-time);
}

.fadeInUp.animate-open {
    opacity: 1;
    transform: translateY(0);
    transition: opacity var(--transition-time), transform var(--transition-time);
}

