.disable-scroll {
    position: relative;
    overflow: hidden;
    height: 100vh;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
}


.modal-close {
    width: 15px;
    height: 15px;
    border-radius: 50%;
    background-color: var(--close-button);
    position: relative;
    &::before {
        content: '';
        position: absolute;
        width: 8px;
        height: 1px;
        margin: auto;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: grey;
        transform: rotate(45deg);
    }
    &::after {
        content: '';
        position: absolute;
        width: 8px;
        height: 1px;
        margin: auto;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: grey;
        transform: rotate(135deg);
    }
}

.modal__content {
    margin: 0 auto;
    max-width: 500px;
    display: grid;
    justify-items: center;
    justify-content: center;
    gap: 25px;
}

.modal__contact {
    display: grid;
    gap: 10px;
    text-align: center;
    justify-items: center;
}
