  padding-bottom: 56.25%; }

.window__img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top; }

/* boxes */
.container {
  width: 100%;
  touch-action: none;
  overflow: hidden;
  background: #ffffff url("../img/bg-picture-v2.png") center/contain no-repeat; }

@media screen and (min-width: 768px) {
  .item {
    position: absolute; } }

.boxx {
  width: 60px;
  border-radius: 5px;
  color: #fff;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  text-align: center;
  user-select: none;
  transition: box-shadow 0.1s;
  cursor: pointer; }

.shortcut {
  font-size: 13px;
  line-height: 1.1;
  color: #4D4D4D;
  pointer-events: none; }
  .shortcut p {
    margin-top: 10px;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis; }
  .shortcut__img {
    width: 60px;
    height: 50px; }

.loader {
  position: absolute;
  z-index: 1;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none; }

.track-big {
  fill: none;
  stroke: #ecebec54;
  stroke-width: 0; }

.progress-big {
  fill: none;
  stroke: white;
  stroke-width: 4;
  stroke-linecap: round;
  stroke-dasharray: 138;
  stroke-dashoffset: 138;
  transform: rotate(90deg);
  transform-origin: center;
  transition: stroke-dashoffset .3s;
  animation: circle-big 5s linear infinite, rotate-big 4s linear infinite; }

@keyframes circle-big {
  100% {
    stroke-dashoffset: -138; }
  50% {
    stroke-dashoffset: 0; }
  0% {
    stroke-dashoffset: 138; } }

@keyframes rotate-big {
  from {
    transform: rotate(-360deg); }
  to {
    transform: rotate(0); } }
